@mixin inputarea {
  &_inputbox{
    align-items: center;
    &_head{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      &_target{
        width: 30%;
      }
      &_explication{
        width: 70%;
        margin: 0px 5px;
      }
    }
    &_buttom{
      &_example{
        width: -webkit-fill-available; 
        margin-right: 5px;
      }
    }
  }
}
.App {
  padding: 30px 10%;
  font-weight: 600;
  &_setting{
    text-align: right;
    font-size: 1.5rem;
    margin-bottom: 20px;
    & > *{
      width: 30px;
      margin-left: 10px;
    }
  }
  &_input{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    margin-bottom: 20px;
    @include inputarea;
    &_listitem{
      &_hidden{
        & >*{
          margin-right: 10px;
        }
      }
    }
  }

  &_modify{
    position: fixed;
    z-index: 10;
    top: 300px;
    background: white;
    box-shadow: 5px 5px 15px -3px rgba(0, 0, 0 , 0.7);
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    font-size: 1.5rem;
    @include inputarea;
    &_submit{
      display: flex;
      align-items: center;
    }
  }
}

.fa-xmark{
  margin-left: 10px;
}

textarea{
  border-radius: 5px;
  border: 1px solid black;
  font-size: 1rem;
}
